<template>
  <nav class="navbar bg-body-tertiary d-block">
    <div class="container">
      <a class="navbar-brand" href="#">        
        Change Case
      </a>
    </div>
  </nav>
  <div class="container pt-5 pb-5">
    <div class="row">
      <div class="col-md-4">
        <label for="txtInput" class="form-label">Case Example</label>
        <div class="card">
          <table class="table mb-0">
            <thead>
              <tr>
                <th class="bg-light text-center">name</th>
                <th class="text-center">example</th>
              </tr>
            </thead>
            <tbody>
              <tr v-for="(value, key) of caseOptions" :key="key">
                <td class="bg-light"><strong>{{ key }}</strong></td>
                <td class="text-end">{{  value }}</td>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
      <div class="col-md-8">
        <div class="row mb-3">               
          <div class="col-md-6">
            <label for="txtInput" class="form-label">Output Case</label>
            <select id="outputCase" v-model="outputCase" class="form-select" :class="[(errMsg.outputCase) ? 'border-danger' : '']">
              <option value="">::output case::</option>
              <option v-for="(value, key) in caseOptionsMap" :key="key" :value="key">
                {{ value }}
              </option>
            </select>
            <div v-if="errMsg.outputCase" class="text-danger mb-3">
              <i class="bi bi-exclamation-circle"></i>
              {{ errMsg.outputCase }}
            </div>
          </div>
        </div>        
        <div class="mb-5">
          <label for="txtInput" class="form-label">Input Text</label>
          <div class="row mb-3">
            <!-- <div class="col">
              <input type="text" class="form-control text-center" placeholder="separator"/>
            </div> -->
            <div class="col">
              <input type="text" class="form-control text-center" v-model.trim="prefixVal" placeholder="prefix"/>
            </div>
            <div class="col">
              <input type="text" class="form-control text-center" v-model.trim="sufixVal" placeholder="sufix"/>
            </div>
            <div class="col">
              <div class="d-grid">
                <button type="button" class="btn btn-primary" @click="onClickChange()">
                  <i class="bi bi-arrow-clockwise"></i> Change
                </button>
              </div>
            </div>
          </div>       
          <textarea rows="10" class="form-control mb-1" :class="[(errMsg.inputTxt) ? 'border-danger' : '']" v-model.trim="inputTxt" id="txtInput" placeholder='To enter multiple words, separate them with ","'></textarea>
          <div v-if="errMsg.inputTxt" class="text-danger">
            <i class="bi bi-exclamation-circle"></i>
            {{ errMsg.inputTxt }}
          </div>
        </div>
        <div class="mb-5">
          <div class="d-flex w-100 align-items-center justify-content-between">
            <label for="txtInput" class="form-label">Output Text</label>            
            <div data-bs-toggle="popover" data-bs-trigger="hover focus" data-bs-content="Copy to clipboard">
              <button type="button" class="btn" @click="onClickCopy()">
                <i class="bi bi-clipboard"></i>
              </button>
            </div>            
          </div>
          <textarea rows="10" class="form-control" id="txtOutput" v-model.trim="outputTxt"></textarea>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup>
import { onMounted, ref } from 'vue';
import * as changeCase from "change-case";
import onCopy from 'copy-to-clipboard';

const caseOptions = {
  camelCase: 'twoWords',
  capitalCase: 'Two Words',
  constantCase:	'TWO_WORDS',
  dotCase: 'two.words',
  kebabCase: 'two-words',
  noCase: 'two words',
  pascalCase: 'TwoWords',
  pascalSnakeCase: 'Two_Words',
  pathCase: 'two/words',
  sentenceCase: 'Two words',
  snakeCase: 'two_words',
  trainCase: 'Two-Words'
}

const inputTxt = ref('')
const outputTxt = ref('')

const outputCase = ref('')

const prefixVal = ref('')
const sufixVal = ref('')

const errMsg = ref({})

const caseOptionsMap = Object.keys(caseOptions).reduce((acc, item)=>{
  acc[item] = item
  return acc
}, {})


const onClickChange = ()=>{
  errMsg.value = {}  
  if(outputCase.value === '') {
    errMsg.value.outputCase = 'Select OutputCase'
    return
  }

  if(inputTxt.value === '') {
    errMsg.value.inputTxt = 'Please enter input text'
    return
  }

  let inputVal = inputTxt.value
  let words = inputVal.split(',')
  words = words.reduce((acc, item)=>{
    acc.push(item.trim())
    return acc
  }, [])

  let execFn = changeCase[outputCase.value]
  if(!execFn) return  
  let changedMap = words.reduce((acc, item)=>{
    acc[item] = execFn(item)
    return acc
  }, {})  
  
  let changedTxt = inputTxt.value
  for(let i=0; i<words.length; i++){
    const beforeVal = words[i]
    const afterVal = changedMap[beforeVal]
    changedTxt = changedTxt.replace(beforeVal, `${prefixVal.value}${afterVal}${sufixVal.value}`)
  }
  outputTxt.value = changedTxt
  
}

const onClickCopy = ()=>{
  if(outputTxt.value === '') return
  onCopy(outputTxt.value)
}

onMounted(()=>{  
  const popoverTriggerList = document.querySelectorAll('[data-bs-toggle="popover"]')
  const popoverList = [...popoverTriggerList].map(function(popoverTriggerEl){
    return new bootstrap.Popover(popoverTriggerEl)
  })  
})
</script>

<style scoped>
label{
  font-weight: bold;
}
.card{
  overflow: hidden
}

.table tbody tr:last-child td{
  border: 0;
  box-shadow: none;
}
</style>
